import { useEffect, useContext } from 'react'

import { StorageKey } from 'Enum'
import { Context } from '../index'
import { type as PlayerType } from '../action/Player'
import { action as LocalStorageAction } from '../middleware/localStorage'

const LocalStorage = () => {
  const store = useContext(Context.Adapter)
  useEffect(() => {
    return store.addDispatchedListener((store, action) => {
      switch (action.type) {
      case PlayerType.UPDATE_VOLUME:
        return store.dispatch(LocalStorageAction.save(StorageKey.VOLUME, {
          ...store.getState().Player.volume,
          volume: action.volume,
        }))
      case PlayerType.MUTED:
        return store.dispatch(LocalStorageAction.save(StorageKey.VOLUME, {
          ...store.getState().Player.volume,
          muted: true,
        }))
      case PlayerType.UNMUTED:
        return store.dispatch(LocalStorageAction.save(StorageKey.VOLUME, {
          ...store.getState().Player.volume,
          muted: false,
        }))
      }
    })
  }, [])
  return null
}

export default LocalStorage
