import { useContext } from 'react'
import PropTypes from 'prop-types'

import { PlayerState } from 'Enum'
import Tooltip from 'player/container/ControlsTooltip'
import Message from 'context/I18n/Message'
import icon from 'style/icon'
import { Context } from 'player/store/index'
import Action from 'player/store/action/Player'
import IconButton from './IconButton'

const ForwardButton = ({ style }) => {
  const { state: { progressTime, seekTime }, dispatch } = useContext(Context.PlayerProgress)
  const { state } = useContext(Context.PlayerState)
  
  return state !== PlayerState.ENDED && (
    <Tooltip title={<Message code="KKS.PLAYER.FORWARD" />} bottom="48px">
      <IconButton
        style={style}
        name="kks-player__forward-button"
        src={icon.forward10}
        onClick={() => dispatch(Action.seek(
          (seekTime !== undefined ? seekTime : progressTime) + 10
        ))}
      />
    </Tooltip>
  )
}

ForwardButton.propTypes = {
  style: PropTypes.object,
}

export default ForwardButton
