import _get from 'dlv'
import { useState, useEffect, useContext, useRef } from 'react'
import PropTypes from 'prop-types'
import Types from 'Types'
import { useCastContext } from 'cast/context'
import { CastState } from 'Enum'
import { getMediaSource } from 'util/mediaSource'
import Action from 'player/store/action'
import { Context } from 'player/store'
import { type as PlayerType } from 'player/store/action/Player'
import operator, { waitActionDispatch } from 'player/store/operator'

const contentEqual = ({ contentType, contentId, licenseId, mediaSource }, state) =>
  _get(state, 'castingMedia.contentId') === contentId &&
  _get(state, 'customData.itemType') === contentType &&
  _get(state, 'customData.licenseId') === licenseId &&
  _get(state, 'customData.mediaSource') === mediaSource

const useDebounced = (value, { timeMs = 500 } = {}) => {
  const [state, setState] = useState(value)
  const timer = useRef({})

  useEffect(() => {
    timer.current = setTimeout(() => setState(value), timeMs)
    return () => clearTimeout(timer.current)
  }, [value])

  return state
}

const CastAdapter = ({
  content = {},
  mediaSource: defaultMediaSource,
  customQuery,
}) => {
  const store = useContext(Context.Adapter)
  const { state, actions: { loadContent }} = useCastContext()
  const lastState = useRef()
  const [
    castState,
    contentType,
    contentId,
    mediaSource,
    licenseId
  ] = useDebounced([
    state.castState,
    content.contentType,
    content.contentId,
    getMediaSource(store.getState(), defaultMediaSource),
    content.licenseId
  ])

  useEffect(() => {
    store.dispatch(Action.castStateChange(castState))
  }, [castState])

  useEffect(() => {
    const contentIsLoaded = contentEqual({ contentId, contentType, licenseId, mediaSource }, state)
    
    switch (castState) {
    case CastState.CONNECTED:
      waitActionDispatch(store, ({ type }) => {
        return type === PlayerType.UNLOADED
      }).then(() => {
        if (!contentIsLoaded) {
          // id, and mediaSource changes are dispatched separately
          // this means to batch these changes
          loadContent({
            contentId,
            mediaSource,
            customQuery,
            licenseId,
            contentType,
          })
        }
      })
      store.dispatch(operator.prepareCast)
      lastState.current = castState
      return
    case CastState.NOT_CONNECTED:
      if (lastState.current === CastState.CONNECTED) {
        store.dispatch(Action.initialized())
      }
      lastState.current = castState
      return
    }
  }, [castState, contentId, contentType, mediaSource, licenseId])

  return null
}

CastAdapter.propTypes = {
  content: Types.VideoInfo,
  mediaSource: PropTypes.string,
  customQuery: PropTypes.object,
}
CastAdapter.defaultProps = {
  content: null,
  mediaSource: null,
  customQuery: {},
}

export default CastAdapter