import { SeekOrigin } from 'Enum'
import icon from 'style/icon'
import { useCastContext } from 'cast/context'
import IconButton from './IconButton'

const ForwardButton = () => {
  const { actions: { seek } } = useCastContext()

  return (
    <IconButton
      src={icon.forward10}
      tooltip="KKS.PLAYER.FORWARD"
      onClick={() => seek({ origin: SeekOrigin.CURRENT, seconds: 10 })}
    />
  )
}

export default ForwardButton
