/* eslint-disable react/prop-types */
/* @jsxImportSource @emotion/react */
import {useRef, forwardRef, Fragment, useEffect, useContext, useMemo} from 'react'
import PropTypes from 'prop-types'

import Types from 'Types'
import { LanguageCode, CastState } from 'Enum'
import defaultConfig from 'config'

import StoreProvider, {Context} from 'player/store/index'
import UiAction from 'player/store/action/UI'

import CastAdapter from 'player/store/adapter/Cast'
import LimitOnePlaybackAdapter from 'player/store/adapter/LimitOnePlayback'
import LocalStorageAdapter from 'player/store/adapter/LocalStorage'
import APIAdapter from 'player/store/adapter/API/Service'
import EventAdapter from 'player/store/adapter/Event'
import Agent from 'player/store/adapter/Agent'
import EnvironmentValidator from 'player/store/adapter/EnvironmentValidator'
import MobileSafariPlugin from 'player/store/adapter/MobileSafariPlugin'
import OTP_1878 from 'player/store/adapter/OTP_1878'
import I18n from 'context/I18n'
import { useCastContext } from 'cast/context'

import DeviceDetecter from 'component/DeviceDetecter'
import PlayerVideo from './PlayerVideo'
import PlayerUi from './container/PlayerUi'
import {PlayerProvider} from './playerContext'
import {useSyncLinearInfo} from './linear'
import {useVodHandlers} from './vod'
import createStore from './store/createStore'

const containerStyle = {
  userSelect: 'none',
  position: 'relative',
  display: 'block',
  width: '100%',
  height: '100%',
  fontSize: '16px',
}

// TODO: these are totally nothing to do with UI structures
// and should be refactored into custom hooks
const PlaybackHooks = ({
  host,
  accessToken,
  deviceId,
  content,
  customHeader,
  customQuery,
  preload,
  autoPlayNext,
  mediaSource,
  thumbnailSeeking,
  supportEnvironmentList,
  limitOnePlaybackAtSameTime,
  recommendation,

  playerRef,
  containerRef,
  onError,
  onBack,
  onVideoQualityChanged,
  onMediaSourceChanged,
  onViewModeChange,
  onEnterFullscreen,
  onExitFullscreen,
  onReplay,
  onChangeVideo,
  onChangeToNextVideo,
  onChangeToPreviousVideo,
  onSectionChange,
}) => {
  const {castState} = useCastContext().state
  const {dispatch} = useContext(Context.Module)
  useSyncLinearInfo()
  useVodHandlers({autoPlayNext})

  useEffect(() => {
    if (recommendation) {
      dispatch(UiAction.enableRecommendationPanel())
    } else {
      dispatch(UiAction.disableRecommendationPanel())
    }
  }, [Boolean(recommendation)])

  useEffect(() => {
    if (thumbnailSeeking) {
      dispatch(UiAction.enableThumbnailSeeking())
    } else {
      dispatch(UiAction.disableThumbnailSeeking())
    }
  }, [Boolean(thumbnailSeeking)])

  return (
    <Fragment>
      {castState !== CastState.CONNECTED && (
        <APIAdapter
          host={host}
          accessToken={accessToken}
          deviceId={deviceId}
          content={content}
          customHeader={customHeader}
          customQuery={customQuery}
          preferredConfig={{
            needThumbnailSeeking: thumbnailSeeking,
            preferredMediaSource: mediaSource,
          }}
        />
      )}
      <LocalStorageAdapter />
      <LimitOnePlaybackAdapter enabled={limitOnePlaybackAtSameTime} />

      <EventAdapter
        content={content}
        onError={onError}
        onBack={onBack}
        onReplay={onReplay}
        onVideoQualityChanged={onVideoQualityChanged}
        onMediaSourceChanged={onMediaSourceChanged}
        onChangeVideo={onChangeVideo}
        onChangeToNextVideo={onChangeToNextVideo}
        onChangeToPreviousVideo={onChangeToPreviousVideo}
        onSectionChange={onSectionChange}
      />
      {castState !== CastState.CONNECTED && (
        <Agent preload={preload} playerRef={playerRef} />
      )}
      <EnvironmentValidator supportEnvironmentList={supportEnvironmentList} />
      <CastAdapter
        content={content}
        mediaSource={mediaSource}
        customQuery={customQuery}
      />
      <DeviceDetecter>
        {({os: {name}}) =>
          name === 'iOS' && (
            <MobileSafariPlugin
              containerRef={containerRef}
              onViewModeChange={onViewModeChange}
              onEnterFullscreen={onEnterFullscreen}
              onExitFullscreen={onExitFullscreen}
            />
          )
        }
      </DeviceDetecter>
      <DeviceDetecter>
        {({os: {name}, touchable}) =>
          name === 'Mac OS' && touchable && <OTP_1878 />
        }
      </DeviceDetecter>
    </Fragment>
  )
}

// eslint-disable-next-line react/display-name
const Player = forwardRef(({
  licenseKey,
  config = defaultConfig.BitmovinConfig,
  host,
  accessToken,
  deviceId,
  content,
  customHeader = {},
  customQuery = {},
  lang = LanguageCode.EN,
  langCustomCode = {},
  preload = 'auto',
  autoPlay,
  autoPlayNext,
  startTime,
  quality,
  mediaSource,
  thumbnailSeeking,
  supportEnvironmentList,
  limitOnePlaybackAtSameTime,
  recommendation,
  toolPanels = [],
  coverImageUrl,
  widevine,
  plugins = [],

  onSourceLoaded,
  onReady,
  onPlay,
  onPaused,
  onPlaying,
  onEnded,
  onError,
  onBack,
  onTimeChanged,
  onSeek,
  onSeeked,
  onVolumeChanged,
  onMuted,
  onUnmuted,
  onVideoQualityChanged,
  onMediaSourceChanged,
  onViewModeChange,
  onEnterFullscreen,
  onExitFullscreen,
  onStallStarted,
  onStallEnded,
  onReplay,
  onChangeVideo,
  onChangeToNextVideo,
  onChangeToPreviousVideo,
  onLogging,
  onSectionChange,
}, ref) => {
  const videoRef = useRef()
  const containerRef = useRef()
  const adContainerRef = useRef()
  const options = {
    host,
    accessToken,
    deviceId,
    content,
    customHeader,
    customQuery,
    lang,
    langCustomCode,
    preload,
    autoPlay,
    autoPlayNext,
    quality,
    mediaSource,
    thumbnailSeeking,
    supportEnvironmentList,
    limitOnePlaybackAtSameTime,
    recommendation,
    toolPanels,
    plugins,
  }
  const store = useMemo(() => createStore(), [])

  return (
    <div
      className="kks-player"
      css={containerStyle}
      ref={containerRef}
    >
      <PlayerProvider videoRef={videoRef} content={content} options={options} uiRef={containerRef} sendLog={onLogging}>
        <StoreProvider store={store} options={options}>
          <PlayerVideo
            licenseKey={licenseKey}
            config={config}
            autoPlay={autoPlay || preload === 'none'}
            startTime={startTime}
            quality={quality}
            containerRef={containerRef}
            widevine={widevine}
            plugins={plugins}
            onSourceLoaded={onSourceLoaded}
            onReady={onReady}
            onPlay={onPlay}
            onPaused={onPaused}
            onPlaying={onPlaying}
            onEnded={onEnded}
            onTimeChanged={onTimeChanged}
            onSeek={onSeek}
            onSeeked={onSeeked}
            onVolumeChanged={onVolumeChanged}
            onMuted={onMuted}
            onUnmuted={onUnmuted}
            onViewModeChange={onViewModeChange}
            onEnterFullscreen={onEnterFullscreen}
            onExitFullscreen={onExitFullscreen}
            onStallStarted={onStallStarted}
            onStallEnded={onStallEnded}
            adContainerRef={adContainerRef}
          />
          <I18n.Provider
            lang={lang}
            langCustomCode={langCustomCode}
          >
            <PlayerUi
              {...{content, autoPlayNext, thumbnailSeeking, recommendation, coverImageUrl}}
              backRef={adContainerRef}
            />
          </I18n.Provider>
          <PlaybackHooks {...{
            ...options,
            playerRef: ref,
            containerRef,
            onError,
            onBack,
            onVideoQualityChanged,
            onMediaSourceChanged,
            onViewModeChange,
            onEnterFullscreen,
            onExitFullscreen,
            onReplay,
            onChangeVideo,
            onChangeToNextVideo,
            onChangeToPreviousVideo,
            onSectionChange,
          }}/>
        </StoreProvider>
      </PlayerProvider>
    </div>
  )
})

Player.propTypes = {
  licenseKey: PropTypes.string.isRequired,
  host: PropTypes.string,
  accessToken: PropTypes.string,
  deviceId: PropTypes.string,
  content: Types.VideoInfo,
  lang: Types.LanguageCode,
  langCustomCode: PropTypes.object,
  customHeader: PropTypes.object,
  customQuery: PropTypes.object,
  thumbnailSeeking: PropTypes.bool,
  limitOnePlaybackAtSameTime: PropTypes.bool,
  preload: PropTypes.oneOf(['auto', 'none']),
  autoPlay: PropTypes.bool,
  autoPlayNext: PropTypes.bool,
  startTime: PropTypes.number,
  quality: PropTypes.string,
  mediaSource: PropTypes.string,
  supportEnvironmentList: PropTypes.arrayOf(Types.SupportEnvironmentItem),
  config: PropTypes.object,
  toolPanels: PropTypes.array,
  recommendation: PropTypes.oneOfType([PropTypes.object, PropTypes.bool]),
  coverImageUrl: PropTypes.string,
  widevine: PropTypes.string,
  plugins: PropTypes.array,

  onError: PropTypes.func,
  onSourceLoaded: PropTypes.func,
  onReady: PropTypes.func,
  onPlay: PropTypes.func,
  onPlaying: PropTypes.func,
  onSeek: PropTypes.func,
  onSeeked: PropTypes.func,
  onPaused: PropTypes.func,
  onTimeChanged: PropTypes.func,
  onVolumeChanged: PropTypes.func,
  onMuted: PropTypes.func,
  onUnmuted: PropTypes.func,
  onStallStarted: PropTypes.func,
  onStallEnded: PropTypes.func,
  onReplay: PropTypes.func,
  onVideoQualityChanged: PropTypes.func,
  onMediaSourceChanged: PropTypes.func,
  onEnded: PropTypes.func,
  onEnterFullscreen: PropTypes.func,
  onExitFullscreen: PropTypes.func,
  onViewModeChange: PropTypes.func,
  onChangeVideo: PropTypes.func,
  onChangeToNextVideo: PropTypes.func,
  onChangeToPreviousVideo: PropTypes.func,
  onBack: PropTypes.func,
  onSectionChange: PropTypes.func,
  onSourceUnloaded: PropTypes.func,
  onLogging: PropTypes.func,
}

export default Player
