import classnames from 'classnames'
import { useContext } from 'react'
import PropTypes from 'prop-types'

import icon from 'style/icon'
import IconButton from './IconButton'
import Tooltip from 'player/container/ControlsTooltip'
import Message from 'context/I18n/Message'
import { Context } from 'player/store/index'
import PlayerAction from 'player/store/action/Player'
import { PlayerViewMode } from 'Enum'

const FullScreenButton = ({style}) => {
  const { state, dispatch } = useContext(Context.PlayerViewMode)
  const src = 
    state === PlayerViewMode.INLINE ?
      icon.enterFullScreen :
      icon.leaveFullScreen
  const tooltip =
    state === PlayerViewMode.INLINE
      ? 'KKS.PLAYER.FULLSCREEN'
      : 'KKS.PLAYER.FULLSCREEN.EXIT'
  const onClick = () => {
    dispatch(
      state === PlayerViewMode.INLINE?
        PlayerAction.enterFullScreen():
        PlayerAction.leaveFullScreen()
    )
  }
  
  return (
    <Tooltip title={<Message code={tooltip} />} bottom="48px">
      <IconButton
        name={classnames(
          'kks-player__full-screen-button',
          `kks-player__full-screen-button--${
            state === PlayerViewMode.INLINE ? 'inline' : 'fullscreen'
          }`
        )}
        src={src}
        onClick={onClick}
        style={style}
      />
    </Tooltip>
  )
}

FullScreenButton.propTypes = {
  style: PropTypes.object,
}

export default FullScreenButton
