import { type as APIType } from 'player/store/action/API'
import { type as playerType } from 'player/store/action/Player'
import { type } from 'player/store/action/UI'

export const initState = {
  qualityItems: [],
  selectedQualityName: undefined,
  qualityPrecedence: 'application',
  mediaSources: [],
  selectedMediaSource: null,
  mediaSourcePrecedence: 'application',
  seekbarFocused: false,
  seeking: false,
  seekTime: 0,
  isMenuOpening: false,
  thumbnailSeeking: true,
  recommendationPanel: {
    enabled: false,
    opening: false,
  },
  autoPlayByUI: false,
  autoplayDialogOpening: false,
  hadShownEndRoll: false,
  ad: {},
  error: null
}

const getAdStatus = action => {
  const {adPosition, totalAds, clickThroughUrl} = action.adProgressData

  return {
    position: adPosition,
    total: totalAds,
    skipTimeOffset: action.skipTimeOffset,
    clickThroughUrl
  }
}

export default (state = initState, action) => {
  switch (action.type) {
  case type.SELECT_QUALITY:
    return {
      ...state,
      qualityPrecedence: 'user',
      selectedQualityName: action.to,
    }
  case type.SET_QUALITY_ITEMS:
    return {
      ...state,
      qualityItems: action.items,
    }
  case type.SELECT_MEDIA_SOURCE:
    return {
      ...state,
      mediaSourcePrecedence: 'user',
      selectedMediaSource: action.mediaSource,
    }
  case type.SET_MEDIA_SOURCES:
    return {
      ...state,
      mediaSources: action.items,
    }
  case type.CHANGE_THUMBNAIL_SEEKING:
    return {
      ...state,
      thumbnailSeeking: action.enabled
    }
  case type.CHANGE_RECOMMENDATION_PANEL:
    return {
      ...state,
      recommendationPanel: {
        enabled: action.enabled,
        opening: action.enabled && state.recommendationPanel.opening
      },
      activePanel: action.enabled && state.recommendationPanel.opening ? 'recommendation' : undefined
    }
  case type.TOGGLE_RECOMMENDATION_PANEL:
    return {
      ...state,
      ...(state.recommendationPanel.enabled && {
        recommendationPanel: { 
          ...state.recommendationPanel,
          opening: !state.recommendationPanel.opening
        },
        activePanel: state.recommendationPanel.opening ? undefined : 'recommendation'
      })
    }
  case type.OPEN_PANEL:
    return {
      ...state,
      activePanel: action.panel
    }
  case type.HIDE_PANEL:
    return {
      ...state,
      activePanel: undefined
    }
  case type.OFFER_AUTOPLAY:
    return {
      ...state,
      isMenuOpening: false,
      autoplayDialogOpening: true,
      activePanel: undefined,
      recommendationPanel: { 
        ...state.recommendationPanel,
        opening: false
      },
      hadShownEndRoll: true,
    }
  case type.RESET_END_ROLL:
    return {
      ...state,
      hadShownEndRoll: false,
    }
  case type.DISMISS_AUTOPLAY:
    return {
      ...state,
      autoplayDialogOpening: false,
      openRecommendationAfterEnd: false,
      ...(state.openRecommendationAfterEnd && state.recommendationPanel.enabled && {
        activePanel: 'recommendation',
        recommendationPanel: { enabled: true, opening: true}
      })
    }
  case type.SHOW_MENU:
    return {
      ...state,
      isMenuOpening: true,
    }
  case type.CHANGE_SETTINGS:
    return (
      {
        ...state,
        isMenuOpening: false,
      }
    )
  case type.HIDE_MENU:
    return {
      ...state,
      isMenuOpening: false
    }
  case type.SET_AUTO_PLAY:
    return {
      ...state,
      autoPlayByUI: action.autoPlay
    }
  case type.ERROR:
    return {
      ...state,
      isMenuOpening: false,
      error: action.error,
    }
  case type.FOCUS_SEEK_BAR:
    return {
      ...state,
      seekbarFocused: true,
      seekTime: action.seekTime
    }
  case type.DRAG:
  case type.DRAG_SEEK_BAR:
    return {
      ...state,
      seeking: true
    }
  case type.DROP:
  case type.DROP_SEEK_BAR:
    return {
      ...state,
      seeking: false
    }
  case type.LEAVE_SEEK_BAR:
    return {
      ...state,
      seekbarFocused: false
    }
  case type.CHANGE_NEXT_EPISODE:
    return {
      ...state,
      openRecommendationAfterEnd: false,
      autoplayDialogOpening: false,
    }
  case APIType.CONTENT_CHANGE:
    return {
      ...state,
      ad: {},
      streamEvents: [],
      openRecommendationAfterEnd: false,
      activePanel: undefined,
      recommendationPanel: { ...state.recommendationPanel, opening: false} 
    }
  case playerType.ENDED:
    return {
      ...state,
      openRecommendationAfterEnd: true,
      ...(!state.autoplayDialogOpening && state.recommendationPanel.enabled && {
        activePanel: 'recommendation',
        recommendationPanel: { enabled: true, opening: true}
      }),
    }
  case type.STREAM_EVENTS_CHANGED:
    return {
      ...state,
      streamEvents: action.streamEvents
    }
  case type.AD_BREAK_STARTED:
    return {
      ...state,
      ad: getAdStatus(action)
    }
  case type.AD_BREAK_ENDED:
    return {
      ...state,
      ad: {
        total: 0
      }
    }
  default:
    return state
  }
}
