/* @jsxImportSource @emotion/react */
import {memo} from 'react'
import PropTypes from 'prop-types'

import formattedTime from 'util/formattedTime'

const containerStyle = {
  position: 'absolute',
  textAlign: 'center',
}

const imageStyle = {

  border: '1px solid white',
  boxShadow: '0 0 5px 2px rgba(0, 0, 0, 0.3)',
  backgroundColor: '#000',
}

const VideoThumbnail = ({className, style, time, image, x, y, width, height}) => (
  <div css={containerStyle} className={className} style={style}>
    <div css={imageStyle} style={{
      width: `${width}px`,
      height: `${height}px`,
      backgroundImage: `url(${image})`,
      backgroundPosition: `-${x}px -${y}px`,
    }} />
    {formattedTime(time)}
  </div>
)

VideoThumbnail.propTypes = {
  className: PropTypes.string,
  style: PropTypes.object,
  time: PropTypes.number,
  image: PropTypes.string,
  x: PropTypes.number,
  y: PropTypes.number,
  width: PropTypes.number,
  height: PropTypes.number,
}

export default memo(VideoThumbnail)