import icon from 'style/icon'
import { useCastContext } from 'cast/context'
import IconButton from './IconButton'

const NextEpisodeButton = () => {
  const { actions: { hasNext, changeNextEpisode } } = useCastContext()

  return hasNext() && (
    <IconButton
      src={icon.nextEpisode}
      tooltip="KKS.PLAYER.NEXT"
      onClick={changeNextEpisode}
    />
  )
}

export default NextEpisodeButton
