import {useContext, useEffect, useImperativeHandle} from 'react'

import Types from 'Types'
import {AgentCommand} from 'Enum'
import { type } from '../action'
import PlayerAction from '../action/Player'
import { type as APIType } from '../action/API'
import UIAction, { type as UIType } from '../action/UI'
import { Context } from '../index'
import operator from '../operator'

const Agent = ({command, preload = 'auto', playerRef}) => {
  const store = useContext(Context.Adapter)
  useEffect(() => {
    store.dispatch(operator.initialize)
  }, [])

  useImperativeHandle(playerRef, () => ({
    load: () => store.dispatch(operator.loadContent)
  }))

  useEffect(() => {
    if (command === AgentCommand.AUTO)
      return store.addDispatchedListener((store, action) => {
        switch (action.type) {
        case type.ERROR:
          store.dispatch(operator.error)
          break
        case type.INITIALIZED:
          if (preload === 'auto') {
            store.dispatch(operator.loadContent)
          }
          break
        case APIType.CLEAN:
          store.dispatch(UIAction.resetEndRoll())
          break
        case APIType.ENABLE:
          store.dispatch(UIAction.resetEndRoll())
          store.dispatch(operator.retry)
          break
        case UIType.CHANGE_NEXT_EPISODE:
        case UIType.CHANGE_PREVIOUS_EPISODE:
          store.dispatch(UIAction.setAutoPlay(true))
          break
        case type.READY_TO_PLAY:
          if (store.getState().UI.autoPlayByUI) {
            store.dispatch(UIAction.setAutoPlay(false))
            store.dispatch(PlayerAction.play())
          }
        }
      })
  }, [])
  return null
}

Agent.propTypes = {
  command: Types.AgentCommand,
}

Agent.defaultProps = {
  command: AgentCommand.AUTO
}

export default Agent
