import {useContext} from 'react'

import {getTargetQuality} from './adaptation'
import {usePlayer} from './playerContext'
import {Context} from './store'
import operator from './store/operator'

const shouldShowVersions = mediaSources =>
  mediaSources.length > 1 || mediaSources[0]
const shouldShowQuality = qualities => qualities.length > 1 || qualities[0]

const defaultQualityName = (items, quality) => {
  const targetHeight = getTargetQuality(
    items.map(it => it.height),
    parseInt(quality, 10)
  )
  return items.find(it => it.height === targetHeight)?.value
}

const speedItems = [0.25, 0.5, 0.75, 1, 1.25, 1.5, 1.75, 2].map(value => ({
  label: `${value}x`,
  value
}))

const getSettingSections = ({
  contentType,
  mediaSources = [],
  playingSourceType,
  qualityItems = [],
  selectedQualityName,
  playbackRate
}) => [
  shouldShowVersions(mediaSources) && {
    section: 'mediaSource',
    title: 'KKS.SETTING.VERSION',
    selectedValue: playingSourceType,
    items: mediaSources.map(value => ({value})),
  },
  shouldShowQuality(qualityItems) && {
    section: 'quality',
    title: 'KKS.QUALITY',
    selectedValue: selectedQualityName,
    items: qualityItems,
  },
  contentType !== 'lives' && {
    section: 'speed',
    title: 'KKS.SETTING.SPEED',
    selectedValue: playbackRate,
    items: speedItems
  }
].filter(Boolean)

const useSettings = () => {
  const {options} = useContext(Context.Module)
  const {
    state: {content, playingSourceType},
  } = useContext(Context.API)
  const {
    state: {
      qualityItems,
      mediaSources,
      selectedQualityName: defaultQuality = defaultQualityName(
        qualityItems,
        options.quality
      ),
    },
  } = useContext(Context.UI)
  const player = usePlayer()

  const selectedQualityName = (
    qualityItems.find(item => item.value === defaultQuality) || qualityItems[0]
  )?.value

  return getSettingSections({
    contentType: content?.contentType,
    mediaSources,
    playingSourceType,
    qualityItems,
    selectedQualityName,
    playbackRate: player?.getPlaybackSpeed()
  })
}

const useChangeSettings = () => {
  const {dispatch} = useContext(Context.Module)
  const player = usePlayer()

  return ({section, value}) => {
    if (section === 'mediaSource' || !player.hasEnded()) {
      player.play()
    }
    if (section === 'quality') {
      dispatch(operator.changeQuality(value))
    }
    if (section === 'mediaSource') {
      dispatch(operator.changeMediaSource(value))
    }
    if (section === 'speed') {
      player.setPlaybackSpeed(value)
    }
  }
}

export {useChangeSettings, useSettings, getSettingSections}
