import create from 'zustand/vanilla'
import createHook from 'zustand'
import {redux, devtools} from 'zustand/middleware'

import { ModuleState } from 'Enum'
import logger from './middleware/logger'
import thunk from './middleware/thunk'
import createGate from './middleware/createGate'
import localStorage from './middleware/localStorage'
import { type } from './action/index'
import { type as APIType } from './action/API'
import { type as PlayerType } from './action/Player'
import reducer from './reducer'
import operator from './operator'

export default () => {
  let state = reducer({}, {type: 'DEFAULT_VALUE' })
  let changeListeners = []
  let dispatchListeners = []
  const store = {}
  const errorGate = createGate((state, action) =>
    state.Module.state !== ModuleState.ERROR ||
    action === operator.error ||
    action.type === APIType.CLEAN ||
    action.type === PlayerType.UNLOAD ||
    action.type === PlayerType.UNLOADED ||
    action.type === PlayerType.LEAVE_FULL_SCREEN ||
    action.type === type.BACK
  )
  const middlewares = [localStorage, errorGate, thunk, logger]
  
  const applyMiddlewares = middlewares
    .map(middleware => middleware(store))
    .concat(action => action)
    .reduceRight((a, b) => b(a))
  const createRegister = (listeners) => (callback) => {
    listeners.push(callback)
    return () => {
      const index = listeners.indexOf(callback)
      if (index >= 0) listeners.splice(index, 1)
    }
  }
  const zustand = create(devtools(redux(reducer, reducer({}, {type: 'DEFAULT_VALUE'}))))

  return Object.assign(store, {
    zustand,
    useStore: createHook(zustand),
    dispatch: (action) => {
      const oldState = state
      const finalAction = applyMiddlewares(action) || action
      state = reducer(state, finalAction)
      dispatchListeners.slice().forEach(callback => callback(store, finalAction))
      if (state !== oldState) {
        changeListeners.slice().forEach(callback => callback(store, action))
      }
      zustand.getState().dispatch(finalAction)

      return state
    },
    getState: () => state,
    addChangedListener: createRegister(changeListeners),
    addDispatchedListener: createRegister(dispatchListeners),
  })
}
