import Action from './index'

export const type = {
  CHANGE_NEXT_EPISODE: 'UI_CHANGE_NEXT_EPISODE',
  CHANGE_PREVIOUS_EPISODE: 'UI_CHANGE_PREVIOUS_EPISODE',
  SELECT_QUALITY: 'UI_SELECT_QUALITY',
  SET_QUALITY_ITEMS: 'UI_SET_QUALITY_ITEMS',
  SELECT_MEDIA_SOURCE: 'UI_SELECT_MEDIA_SOURCE',
  SET_MEDIA_SOURCES: 'UI_SET_MEDIA_SOURCES',
  CHANGE_THUMBNAIL_SEEKING: 'CHANGE_THUMBNAIL_SEEKING',
  CHANGE_RECOMMENDATION_PANEL: 'CHANGE_RECOMMENDATION_PANEL',
  TOGGLE_RECOMMENDATION_PANEL: 'TOGGLE_RECOMMENDATION_PANEL',
  OPEN_PANEL: 'OPEN_PANEL',
  HIDE_PANEL: 'HIDE_PANEL',
  OFFER_AUTOPLAY: 'OFFER_AUTOPLAY',
  DISMISS_AUTOPLAY: 'DISMISS_AUTOPLAY',
  SHOW_MENU: 'UI_SHOW_MENU',
  HIDE_MENU: 'UI_HIDE_MENU',
  CHANGE_SETTINGS: 'UI_CHANGE_SETTINGS',
  ERROR: 'UI_ERROR',
  DRAG: 'UI_DRAG',
  DROP: 'UI_DROP',
  FOCUS_SEEK_BAR: 'UI_FOCUS_SEEK_BAR',
  DRAG_SEEK_BAR: 'UI_DRAG_SEEK_BAR',
  DROP_SEEK_BAR: 'UI_DROP_SEEK_BAR',
  LEAVE_SEEK_BAR: 'UI_LEAVE_SEEK_BAR',
  SET_AUTO_PLAY: 'UI_SET_AUTO_PLAY',
  RESET_END_ROLL: 'RESET_END_ROLL',
  STREAM_EVENTS_CHANGED: 'STREAM_EVENTS_CHANGED',
  AD_BREAK_STARTED: 'AD_BREAK_STARTED',
  AD_BREAK_ENDED: 'AD_BREAK_ENDED',
  VISIBILITY_CHANGE: 'VISIBILITY_CHANGE',
}
export default {
  changeNextEpisode: (videoId) => ({ type: type.CHANGE_NEXT_EPISODE, videoId }),
  changePreviousEpisode: (videoId) => ({ type: type.CHANGE_PREVIOUS_EPISODE, videoId }),
  selectQuality: (to, from) => ({ type: type.SELECT_QUALITY, to, from }),
  setQualityItems: (items) =>
    ({ type: type.SET_QUALITY_ITEMS, items }),
  selectMediaSource: (mediaSource) => ({ type: type.SELECT_MEDIA_SOURCE, mediaSource }),
  setMediaSources: (items = []) =>
    ({ type: type.SET_MEDIA_SOURCES, items }),
  enableRecommendationPanel: () => ({ type: type.CHANGE_RECOMMENDATION_PANEL, enabled: true }),
  disableRecommendationPanel: () => ({ type: type.CHANGE_RECOMMENDATION_PANEL, enabled: false }),
  toggleRecommendationPanel: () => ({ type: type.TOGGLE_RECOMMENDATION_PANEL }),
  openPanel: panel => ({type: type.OPEN_PANEL, panel}),
  hidePanel: () => ({type: type.HIDE_PANEL}),
  enableThumbnailSeeking: () => ({ type: type.CHANGE_THUMBNAIL_SEEKING, enabled: true }),
  disableThumbnailSeeking: () => ({ type: type.CHANGE_THUMBNAIL_SEEKING, enabled: false }),
  offerAutoplay: () => ({ type: type.OFFER_AUTOPLAY }),
  dismissAutoplay: () => ({ type: type.DISMISS_AUTOPLAY }),
  showTooltip: (id, border, message) =>
    ({ type: type.SHOW_TOOLTIP, message, border, id }),
  updateTooltip: (id, message) => ({ type: type.UPDATE_TOOLTIP, message, id }),
  showMenu: () => ({ type: type.SHOW_MENU }),
  hideMenu: (id) => ({ type: type.HIDE_MENU, id }),
  changeSettings: ({ id, section, value }) => ({ type: type.CHANGE_SETTINGS, section, value, id }),
  showThumbnail: (id, border, thumbnailTime) => ({ type: type.SHOW_THUMBNAIL, thumbnailTime, border, id }),
  focusSeekBar: (seekTime) => ({ type: type.FOCUS_SEEK_BAR, seekTime }),
  dragSeekBar: () => ({ type: type.DRAG_SEEK_BAR }),
  dropSeekBar: () => ({ type: type.DROP_SEEK_BAR }),
  leaveSeekBar: () => ({ type: type.LEAVE_SEEK_BAR }),
  drag: () => ({ type: type.DRAG }),
  drop: () => ({ type: type.DROP }),
  setAutoPlay: (value) => ({ type: type.SET_AUTO_PLAY, autoPlay: value }),
  resetEndRoll: (value) => ({ type: type.RESET_END_ROLL, autoPlay: value }),
  streamEventsChanged: (streamEvents, playbackStatus) => ({
    type: type.STREAM_EVENTS_CHANGED,
    streamEvents,
    playbackStatus
  }),
  adBreakStarted: (adProgressData, skipTimeOffset) => ({
    type: type.AD_BREAK_STARTED,
    adProgressData,
    skipTimeOffset
  }),
  adBreakEnded: () => ({type: type.AD_BREAK_ENDED}),
  visibilityChange: () => ({type: type.VISIBILITY_CHANGE}),
  error: (error) => Action.error('UI', error),
}
