import {useContext} from 'react'

import {Context} from 'player/store'

const useThumbnail = time => {
  const {state} = useContext(Context.API)
  const thumbnailSeekings = state.thumbnailSeekings || []

  return (
    thumbnailSeekings.find(
      t => t.startTime <= time && time <= t.endTime
    ) || thumbnailSeekings.slice(-1)[0] || ''
  )
}

export default useThumbnail
