import { type } from '../action/Player'
import {type as uiType} from 'player/store/action/UI'
import { PlayerState, PlayerViewMode} from 'Enum'
import { scope } from 'util/index'

export const initState = {
  playerState: PlayerState.UNMOUNTED,
  progress: {
    progressTime: 0,
    duration: 0,
    bufferTime: 0,
  },
  volume: {
    volume: 100,
    muted: false,
  },
  viewMode: PlayerViewMode.INLINE,
  streamType: null,
  source: null,
  userAction: 'pause',
  isPlaying: false,
  seeking: false,
  ended: false,
  playingQuality: null
}
const mergePlayerState = (state, playerState, other) => ({
  ...state,
  playerState,
  ...other,
})
const mergeProgress = (state, progress, other) => ({
  ...state,
  progress: {
    ...state.progress,
    ...progress,
  },
  ...other,
})
const mergeVolume = (state, volume, other) => ({
  ...state,
  volume: {
    ...state.volume,
    ...volume,
  },
  ...other,
})
const mergeViewMode = (state, viewMode, other) => ({
  ...state,
  viewMode,
  ...other,
})

export default (state = initState, action) => {
  switch (action.type) {
  case type.MOUNT:
    return mergePlayerState(initState, PlayerState.MOUNTING)
  case type.MOUNTED:
    return mergePlayerState(state, PlayerState.MOUNTED)
  case type.UNLOAD:
    return mergePlayerState(state, PlayerState.LOADING, {ended: false, progress: initState.progress})
  case type.UNLOADED:
    return mergePlayerState(state, PlayerState.MOUNTED, { source: null })
  case type.READY:
    if (state.playerState === PlayerState.PLAYING)
      return state
    return mergePlayerState(state, PlayerState.READY)
  case type.SET_STATE:
    return { ...state, ...action.state }
  case type.PLAY:
    return state.isPlaying ? state : {
      ...state,
      ...state.ended && {
        progress: {...state.progress, progressTime: 0},
        ended: false,
      },
      userAction: 'play',
      playerState: PlayerState.STALLING
    }
  case type.PAUSE:
    return {
      ...state,
      userAction: 'pause',
    }
  case type.PAUSED:
    return {
      ...state,
      ...state.playerState !== PlayerState.STALLING && {
        playerState: PlayerState.PAUSED,
      },
      isPlaying: false,
    }
  case type.ENDED:
    return mergePlayerState(state, PlayerState.ENDED, { isPlaying: false, ended: true })
  case type.SEEK:
    return mergeProgress(
      state,
      { seekTime: scope(0, action.time, state.progress.duration) },
      { playerState: PlayerState.STALLING, seeking: true }
    )
  case type.SEEKED:
    return mergeProgress(
      state,
      { seekTime: undefined, progressTime: action.time },
      {
        ...state.userAction === 'pause' && {
          playerState: PlayerState.PAUSED
        },
        seeking: false,
      }
    )
  case type.UPDATE_CURRENT_TIME:
    return mergeProgress(state, {
      seekTime: undefined,
      progressTime: action.time,
      adRemainingTime: action.adRemainingTime
    }, {
      playerState: PlayerState.PLAYING,
      userAction: 'play',
      isPlaying: true,
      seeking: false
    })
  case uiType.VISIBILITY_CHANGE:
    return mergePlayerState(state, PlayerState.STALLING)
  case type.UPDATE_BUFFER_TIME:
    return mergeProgress(state, { bufferTime: action.time })
  case type.UPDATE_VOLUME:
    return mergeVolume(state, { volume: action.volume })
  case type.MUTED:
    return mergeVolume(state, { muted: true })
  case type.UNMUTED:
    return mergeVolume(state, { muted: false })
  case type.UPDATE_VIEW_MODE:
    return mergeViewMode(state, action.mode)
  case type.STALLING:
    return mergePlayerState(state, PlayerState.STALLING)
  case type.STALLED:
    return mergePlayerState(state, state.isPlaying ? PlayerState.PLAYING : PlayerState.PAUSED)
  default:
    return state
  }
}
