/* @jsxImportSource @emotion/react */
import {cloneElement} from 'react'
import PropTypes from 'prop-types'

const style = {
  display: 'flex',
  flexDirection: 'column',
  overflow: 'visible',
  height: '100%',
  width: '100%',
  position: 'absolute',
  bottom: '0',
  backgroundColor: 'rgba(0, 0, 0, 0)',
  transition: 'background-color 0.5s ease, transform 0s 0.5s',
  transform: 'translateY(100%)',
  '> *:last-of-type': {
    flex: '0 auto',
    transition: 'transform 0.5s ease',
    transform: 'translateY(100%)',
  },
}

const activeStyle = {
  backgroundColor: 'rgba(0, 0, 0, 0.6)',
  transition: 'background-color 0.5s ease',
  transform: 'translateX(0)',
  '> *:last-of-type': {
    transform: 'translateY(0)',
  },
}

const foldStyle = height => height && {
  '> *:last-of-type': {
    // workaround IE 11 CSS calc() bug
    transform: `translateY(100%) translateY(-${height})`,
  }
}

const ToolPanel = ({active, foldedHeight = '0', children, toggle, close}) => (
  <div css={[style, active ? activeStyle : foldStyle(foldedHeight)]}>
    <div
      style={{flex: 1}}
      onClick={event => {
        event.stopPropagation()
        close(event)
      }}
    />
    {cloneElement(children[0] || children, {active, toggle, close})}
  </div>
)

ToolPanel.propTypes = {
  index: PropTypes.number,
  active: PropTypes.bool,
  foldedHeight: PropTypes.string,
  children: PropTypes.node,
  toggle: PropTypes.func,
  close: PropTypes.func,
}

export default ToolPanel
