/* @jsxImportSource @emotion/react */
// eslint-disable-next-line no-unused-vars
import PropTypes from 'prop-types'

import icon from 'style/icon'

const openIcon = {
  width: '1rem',
  height: '1rem',
  margin: '0 0.5rem',
  fontWeight: 'bold',
  backgroundImage: `url(${icon.arrowTop})`,
  transition: 'transform 0.5s',
}

const closeIcon = {
  ...openIcon,
  transform: 'rotateX(180deg)'
}

const titleStyle = {
  padding: '0.5rem 2.5rem',
  display: 'flex',
  alignItems: 'center',
  color: '#ccc',
  fontSize: '20px',
  cursor: 'pointer',
}

const contentStyle = {
  overflow: 'auto',
}

const contentHiddenStyle = {
  overflow: 'hidden',
  pointerEvents: 'none',
  touchAction: 'none',
}

const RecommendationBox = ({opening, title, children = '', toggle}) => (
  <>
    <div css={titleStyle} onClick={opening && toggle}>
      {title}
      <div css={[opening ? closeIcon : openIcon]} />
    </div>
    <div css={[contentStyle, !opening && contentHiddenStyle]}>
      {children}
    </div>
  </>
)

RecommendationBox.propTypes = {
  opening: PropTypes.bool,
  toggle: PropTypes.func,
  title: PropTypes.node,
  children: PropTypes.node,
}

const foldedHeight = '5rem'

const style = {
  container: {
    position: 'absolute',
    bottom: '0',
    width: '100%',
    overflow: 'hidden',
    transition: 'transform 0.5s ease',
    transform: 'translateY(0)',
  },
  folded: {
    transform: `translateY(100%) translateY(-${foldedHeight})`,
  }
}

const RecommendationPanel = ({ opening, toggle, title, children = null }) => {
  return children && (
    <div
      className="kks-player__recommendation"
      css={[style.container, !opening && style.folded]}
      onClick={() => {
        if (!opening) {
          toggle()
        }
      }}
    >
      <RecommendationBox opening={opening} title={title} toggle={toggle}>
        {children}
      </RecommendationBox>
    </div>
  )
}

RecommendationPanel.propTypes = {
  opening: PropTypes.bool,
  toggle: PropTypes.func,
  title: PropTypes.node,
  children: PropTypes.node,
}

export default RecommendationPanel