/* eslint-disable indent */
import {ModuleState, PlayerState, CastState} from 'Enum'
import formattedTime from 'util/formattedTime'
import {convertToTimeString} from 'util/index'

const getPlaybackInfo = ({Module, API, Player, UI}) => {
  const {contentType, title, subtitle, end, start_time: startTime, end_time: endTime} =
    API.content || {}
  const {seeking, seekbarFocused, isMenuOpening, activePanel, ad} = UI
  const status =
    Module.state === ModuleState.ERROR
      ? 'error'
      : Module.castState === CastState.CONNECTED
      ? 'casting'
      : end
      ? 'linearEnded'
      : Module.state !== ModuleState.READY && Module.state !== ModuleState.INITIALIZED
      ? 'loading'
      : Player.playerState === PlayerState.PLAYING
      ? 'playing'
      : Player.playerState === PlayerState.STALLING
      ? 'buffering'
      : 'paused'
  const ready = Module.state === ModuleState.READY
  const focused =
    seekbarFocused || seeking || isMenuOpening || activePanel != null
  const controlsDisplay = UI.autoplayDialogOpening ? 'hidden' : undefined
  const programTime = startTime
    ? `${convertToTimeString(
        new Date(startTime * 1000),
        'HH:mm'
      )} - ${convertToTimeString(new Date(endTime * 1000), 'HH:mm')}`
    : ''
  const channelTitle = subtitle && `${programTime} ${subtitle}`
  const adRemainingTime = formattedTime(Math.ceil(Player.progress.adRemainingTime))
  const adStatus = ad.total > 0 && `Ad ${ad.position} of ${ad.total}・${adRemainingTime}`
  const adUrl = ad.clickThroughUrl
  const adSkipTimeOffset = API.content?.contentType === 'lives' ? -1 : UI.ad?.skipTimeOffset
  const seekable = contentType === 'videos' && !(ad.total > 0)

  return {
    status,
    ready,
    seekable,
    focused,
    controlsDisplay,
    title,
    channelTitle,
    adStatus,
    adUrl,
    adSkipTimeOffset,
  }
}

export default getPlaybackInfo
