import icon from 'style/icon'
import { useCastContext } from 'cast/context'
import IconButton from './IconButton'

const PreviousEpisodeButton = () => {
  const { actions: { hasPrevious, changePreviousEpisode } } = useCastContext()

  return hasPrevious() && (
    <IconButton
      src={icon.previousEpisode}
      tooltip="KKS.PLAYER.PREVIOUS"
      onClick={changePreviousEpisode}
    />
  )
}

export default PreviousEpisodeButton
