import Action from './index'

export const type = {
  CLEAN: 'API_CLEAN',
  DISABLE: 'API_DISABLE',
  ENABLE: 'API_ENABLE',
  PREVIEW_CONTENT: 'API_PREVIEW_CONTENT',
  PREPARE_PLAYBACK: 'API_PREPARE_PLAYBACK',
  PREPARE_THUMBNAIL: 'API_PREPARE_THUMBNAIL',
  SET_PLAYBACK: 'API_SET_PLAYBACK',
  SET_THUMBNAILS: 'API_SET_THUMBNAILS',
  READY: 'API_READY',
  REFRESH_PLAYBACK_TOKEN: 'API_REFRESH_PLAYBACK_TOKEN',
  UPDATE_LAST_PLAYED_TIME: 'API_UPDATE_LAST_PLAYED_TIME',
  UPDATE_PLAYED_END: 'API_UPDATE_PLAYED_END',
  REFRESH_CONTENT: 'API_REFRESH_CONTENT',
  SET_CONTENT: 'API_SET_CONTENT',
  END_PROGRAM: 'API_END_PROGRAM',
  CHANGE_PROGRAM: 'API_CHANGE_PROGRAM',
  END_SECTION: 'API_END_SECTION',
  CHANGE_SECTION: 'API_CHANGE_SECTION',
  CONTENT_CHANGE: 'API_CONTENT_CHANGE',
  END_LIVE_TYPE: 'API_END_LIVE_TYPE',
}
export default {
  contentChange: () => ({ type: type.CONTENT_CHANGE }),
  clean: () => ({ type: type.CLEAN }),
  disable: () => ({ type: type.DISABLE }),
  enable: (content, customHeader) => ({ type: type.ENABLE, content, customHeader }),
  previewContent: () => ({ type: type.PREVIEW_CONTENT }),
  preparePlayback: () => ({ type: type.PREPARE_PLAYBACK }),
  prepareThumbnail: () => ({ type: type.PREPARE_THUMBNAIL }),
  setPlayback: (playbackInfo) => ({ type: type.SET_PLAYBACK, playbackInfo }),
  setThumbnails: (thumbnails) => ({ type: type.SET_THUMBNAILS, thumbnails }),
  ready: () => ({ type: type.READY }),
  refreshPlaybackToken: sessionData => ({ type: type.REFRESH_PLAYBACK_TOKEN, sessionData }),
  updateLastPlayedTime: (time) => ({ type: type.UPDATE_LAST_PLAYED_TIME, time }),
  updatePlayedEnd: () => ({ type: type.UPDATE_PLAYED_END }),
  refreshContent: () => ({ type: type.REFRESH_CONTENT }),
  setContent: (content) => ({ type: type.SET_CONTENT, content }),
  endProgram: () => ({ type: type.END_PROGRAM }),
  changeProgram: (oldId, newId) => ({ type: type.CHANGE_PROGRAM, oldId, newId }),
  endSection: () => ({ type: type.END_SECTION }),
  changeSection: (oldId, newId) => ({ type: type.CHANGE_SECTION, oldId, newId }),
  endLiveType: () => ({ type: type.END_LIVE_TYPE }),

  error: (error) => Action.error('API', error),
}
