/* @jsxImportSource @emotion/react */
import {useContext} from 'react'
import PropTypes from 'prop-types'
import { Context } from 'player/store'

import { getLiveTime } from 'util/index'
import formattedTime from 'util/formattedTime'

const _css ={
  display: 'flex',
  alignItems: 'center',
  width: '100%',
  fontSize: '0.75em',
  letterSpacing: '1px',
  color: '#fff',
}

const sliderStyle = {
  flex: 1,
  margin: '0 0.5em',
  height: '0.25em',
  backgroundColor: 'red',
}

const LiveSeekbar = ({style}) => {
  useContext(Context.PlayerProgress)
  const { state: { content } } = useContext(Context.API)
  const { duration, currentTime } = getLiveTime(content || {})
  // TODO: share these with sender
  
  return (
    <div className="kks-player__seek-bar" css={[_css, style]}>
      {currentTime > 0 &&
        <>
          {formattedTime(currentTime)}
          <div className="kks-player__slider" css={[sliderStyle, style?.slider]} />
          {formattedTime(duration)}
        </>
      }
    </div>
  )
}
LiveSeekbar.propTypes = {
  style: PropTypes.object,
}

export default LiveSeekbar
