import { useEffect, useContext, useRef } from 'react'
import PropTypes from 'prop-types'
import _get from 'dlv'
import axios from 'axios'

import {useDispatchSessionEvent} from 'player/playerContext'
import Types from 'Types'
import { ItemType } from 'Enum'
import parseThumbnails from 'util/parseThumbnails'
import { useInterval } from 'Hooks'
import { Context } from 'player/store/index'
import Action, { type } from 'player/store/action/API'
import { getMediaSource } from 'util/mediaSource'
import Resource from './Resource'
import Session from './Session'

const HEARTBEAT_INTERVAL = 10000

const API = ({
  host,
  accessToken,
  deviceId,
  content,
  customHeader,
  customQuery,
  preferredConfig: {
    preferredMediaSource,
  },
}) => {
  const session = useRef(null)
  const dispatchSessionEvent = useDispatchSessionEvent()
  const [setInterval, clearInterval] = useInterval()
  const store = useContext(Context.Adapter)
  const isActivatable = (
    host &&
    content &&
    content.contentId !== undefined &&
    content.contentType !== undefined
  )
  useEffect(
    () => {
      if (isActivatable) {
        store.dispatch(Action.enable(content, customHeader))
      } else {
        store.dispatch(Action.disable())
      }

      if (session.current) {
        dispatchSessionEvent('playbackEnded')
        session.current.end()
      }
      session.current = null
    },
    [host, content.contentId, content.contentType]
  )
  useEffect(() => {
    const { contentId, contentType, ...query } = content
    const resource = Resource({ host, accessToken, deviceId, customHeader, customQuery })
    session.current = new Session(resource, contentId, contentType, query)
    const fetchContent = () => (
      ItemType.LIVES === contentType
        ? resource.lives({ id: contentId, query: { ...query, contentType }})
        : resource.videos({ id: contentId, query: { ...query, contentType }})
    )
    const fetchFullPlayback = async () => {
      const state = store.getState()
      const targetSourceType = getMediaSource(state, preferredMediaSource)
      const [playbackToken, contentInfo] = await Promise.all([
        session.current.start(),
        fetchContent()
      ])
      const playbackInfo = await session.current.info()
      const source = 
        playbackInfo.sources.find(source => source.type === targetSourceType) ||
        playbackInfo.sources[0]

      return {
        drm: {
          drm__fairplay: { url: playbackToken.drm_portal_url },
          drm__playready: { url: playbackToken.drm_portal_url },
          drm__widevine: { url: playbackToken.drm_portal_url },
        },
        playingSourceType: source.type,
        availableSources: playbackInfo.sources.map(source => source.type),
        manifests: source.manifests,
        playbackToken,
        customHeader,
        thumbnailSeekingsURL: source.thumbnail_seeking_url,
        ...contentInfo
      }
    }
    const removeListener = store.addDispatchedListener(async (store, action) => {
      if (action && isActivatable) {
        try {
          switch (action.type) {
          case type.CLEAN:
          case type.DISABLE: {
            clearInterval()
            break
          }
          case type.PREVIEW_CONTENT: {
            const data = await fetchContent()
            store.dispatch(Action.setContent(data))
            break
          }
          case type.PREPARE_PLAYBACK: {
            const data = await fetchFullPlayback()
            store.dispatch(Action.setPlayback(data))
            break
          }
          case type.PREPARE_THUMBNAIL: {
            const thumbnailUrl = _get(store.getState(), 'API.content.thumbnailSeekingsURL')
            if (thumbnailUrl) {
              await axios.get(thumbnailUrl)
                .then(result => parseThumbnails(result.data, thumbnailUrl))
                .then(thumbnails => store.dispatch(Action.setThumbnails(thumbnails)))
                .catch(() => {})
            }
            store.dispatch(Action.ready())
            break
          }
          case type.READY: {
            if (accessToken && _get(store.getState(), 'API.playback.heartbeat', false)) {
              clearInterval()
              setInterval(() => {
                if (session.current.heartbeatInProgress) {
                  return
                }
                session.current.heartbeatInProgress = true
                session.current.heartbeat()
                  .catch(async error => {
                    if (error?.response.status === 400) {
                      const sessionData = await session.current.start()
                      store.dispatch(Action.refreshPlaybackToken(sessionData))
                    } else {
                      clearInterval()
                      store.dispatch(Action.error(error))
                    }
                  })
                  .then(() => {
                    session.current.heartbeatInProgress = false
                  })
              }, HEARTBEAT_INTERVAL)
            }
            break
          }
          case type.UPDATE_LAST_PLAYED_TIME: {
            // ignore updatePlaybackTime api error
            session.current.updateLastPlayedTime(action.time)
            break
          }
          case type.UPDATE_PLAYED_END: {
            clearInterval()
            await session.current.updateLastPlayedTime(0)
            dispatchSessionEvent('playbackEnded')
            await session.current.end()
            break
          }
          case type.REFRESH_CONTENT: {
            const newContent = await fetchContent()
            const oldSectionId = _get(store.getState(), 'API.content.section_id')
            const newSectionId = newContent.section_id
            if (oldSectionId !== undefined && oldSectionId !== newSectionId) {
              store.dispatch(Action.endSection())
              store.dispatch(Action.setContent(newContent))
              dispatchSessionEvent('sectionChange')
              store.dispatch(Action.changeSection(oldSectionId, newSectionId))
            } else {
              store.dispatch(Action.setContent(newContent))
            }

            if (_get(store.getState(), 'API.content.end')) {
              store.dispatch(Action.endLiveType())
            }
            break
          }
          }
        } catch(error) {
          store.dispatch(Action.error(error))
        }
      }
    })
    return () => {
      if (session.current && session.current.playbackToken) {
        dispatchSessionEvent('playbackEnded')
        session.current.end()
      }
      removeListener()
      clearInterval()
    }
  }, [host, accessToken, content.contentId, content.contentType])
  return null
}

API.propTypes = {
  host: PropTypes.string,
  accessToken: PropTypes.string,
  deviceId: PropTypes.string,
  content: Types.VideoInfo,
  customHeader: PropTypes.object,
  customQuery: PropTypes.object,
  preferredConfig: PropTypes.shape({
    preferredMediaSource: PropTypes.string,
  })
}

API.defaultProps = {
  host: null,
  accessToken: null,
  deviceId: null,
  content: {},
  customHeader: {},
  customQuery: {},
  preferredConfig: {
    preferredMediaSource: null,
  },
}

export default API
