/* @jsxImportSource @emotion/react */
import _get from 'dlv'

import FormattedTime from 'component/FormattedTime'
import SimpleSlider from 'component/SimpleSlider'
import { useCastContext } from 'cast/context'
import { getLiveTime } from 'util/index'
import {SeekOrigin} from 'Enum'

const sliderStyle = {
  flex: 1,
  margin: '0 0.5rem',
  height: '4px',
  backgroundColor: 'red',
}

const Seekbar = () => {
  const { state, actions: { seek }} = useCastContext()
  const {
    currentTime,
    progressTime = currentTime,
    duration
  } = state.streamType === 'LIVE' ?
    getLiveTime(_get(state, 'customData') || {}) :
    state

  return (
    <div
      className="kks-player__seek-bar"
      css={{
        flex: '100%',
        display: 'flex',
        alignItems: 'center',
        marginBottom: '18px'
      }}>
      <FormattedTime time={progressTime} />
      {state.streamType === 'LIVE' ? (
        <div css={sliderStyle} />
      ) : (
        <SimpleSlider
          css={{margin: '0 1rem', flex: 1}}
          value={progressTime}
          max={duration}
          onChangeCommitted={(_, {value}) => seek({origin: SeekOrigin.START, seconds: value})}
        />
      )}
      <FormattedTime time={Math.max(duration, progressTime)} />
    </div>
  )
}

export default Seekbar