export default class Session {
  constructor(resource, id, type, query) {
    this.resource = resource;
    this.id = id;
    this.type = type;
    this.query = query;
    this.resourceInfo = {
      id: this.id,
      type: this.type,
      query: { ...this.query, type: this.type }
    }
    this.playbackToken = null;
    this._startPromise = null
    this._endPromise = {}
  }

  start() {
    if (this._startPromise === null) {
      this._startPromise = this.resource
        .playbackStart(this.resourceInfo)
        .then(tokenInfo => {
          this.playbackToken = tokenInfo
          this._startPromise = null
          return tokenInfo
        })
    }

    return this._startPromise
  }
  
  info() {
    return this.playbackToken && this.resource.playbackInfo({
      ...this.resourceInfo,
      token: this.playbackToken.token
    })
  }

  heartbeat() {
    return this.playbackToken && this.resource.heartbeat({
      ...this.resourceInfo,
      token: this.playbackToken.token
    })
  }

  end() {
    const token = this.playbackToken && this.playbackToken.token
    if (token && !this._endPromise[token]) {
      this._endPromise[token] = this.resource
        .playbackEnd({ ...this.resourceInfo, token })
        // token might be already expired
        .catch(() => {})
        .then(() => {
          this._endPromise[token] = null
        })
      if (this.playbackToken.token === token) {
        this.playbackToken = null
      }
    }

    return this._endPromise[token];
  }
  
  async restart() {
    await this.end()
    return this.start()
  }

  updateLastPlayedTime(time) {
    return this.playbackToken && this.resource.updateLastPlayedTime({
      ...this.resourceInfo,
      token: this.playbackToken.token,
      position: Math.floor(time)
    })
  }
}